* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  background-color: #f6f6f6;
  font-family: Poppins, sans-serif;
  line-height: 1.2;
  overflow: hidden;
}

.info-icon {
  cursor: pointer;
  border: none;
  border-radius: 50%;
  font-size: 16px;
  transition: background .3s;
  animation-duration: .5s;
  animation-delay: .3s;
  position: absolute;
  top: 85px;
  left: 20px;
}

.info-icon:hover {
  background: #79acff;
  animation-name: jump-icon;
  animation-timing-function: ease;
}

@keyframes jump-icon {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-15px);
  }

  100% {
    transform: translateY(0);
  }
}

.btn {
  background-color: inherit;
  width: 20%;
  border: 1px solid #212121;
  padding: 3px 2px;
  font-weight: 600;
  transition: box-shadow .5s, background .3s;
}

.btn:hover {
  cursor: pointer;
  background-color: #c49c90;
}

.btn__form {
  position: absolute;
  top: 70px;
  right: 20px;
}

.btn__form:hover {
  box-shadow: 0 8px 24px #0006;
}

@media only screen and (max-width: 1600px) {
  .btn__form {
    top: 80px;
    right: 0;
  }
}

@media only screen and (max-width: 1024px) {
  .btn__form {
    top: 67px;
    right: -3px;
  }
}

.input {
  cursor: text;
  height: 19px;
  text-align: center;
  width: 50%;
  background: linear-gradient(to left, #ffffff4d, #fff3);
  border: none;
  border-radius: 10px;
  padding: 2px;
  transition: box-shadow .3s;
  box-shadow: 0 8px 24px #2121211a;
}

.input:hover {
  box-shadow: 0 8px 24px #0006;
}

.input:focus {
  outline: 1px solid #0006;
}

::placeholder {
  color: #212121;
}

.icons {
  grid-template-rows: repeat(2, 30px);
  grid-template-columns: repeat(5, 30px);
  gap: 5px;
  margin-top: 20px;
  display: grid;
}

.icons__icon {
  background: #0003;
  border: none;
  border-radius: 4px;
}

.icons__icon:hover {
  background-color: #890000;
}

.icons__icon:hover:first-child {
  background-color: #2b6b30;
}

.icons__icon:focus {
  background-color: #7f0000;
}

.icons__icon:focus:first-child {
  background-color: #236023;
}

@media only screen and (max-width: 1024px) {
  .icons {
    gap: 3px;
    margin-top: 10px;
  }
}

.navigation {
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 50px;
  background-color: #6f5f90cc;
  align-items: center;
  display: flex;
}

.navigation__welcome {
  flex-grow: 1;
  padding: 0 20px;
}

.navigation__login {
  padding: 0 20px;
  display: flex;
}

.navigation__login > * {
  margin-left: 8px;
}

.info {
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  max-height: 100%;
  width: 680px;
  background: #79acff80;
  border-radius: 10px;
  padding: 30px;
  transition: opacity 1s;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

.info__title {
  margin-bottom: 17px;
}

.info__closeIcon {
  cursor: pointer;
  border: none;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 20px;
  right: 15px;
}

.info__closeIcon:hover {
  background: #79acff;
  transform: scale(1.2);
}

.info__table td {
  padding-left: 30px;
}

.info__success {
  font-size: 26px;
  position: absolute;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.info__form {
  width: 450px;
  flex-direction: column;
  display: flex;
  bottom: 20px;
  transform: translateX(50%);
}

@media only screen and (max-width: 1600px) {
  .info {
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media only screen and (max-width: 1024px) {
  .info {
    font-size: 14px;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.user-panel {
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  background-color: #212121b3;
  border-radius: 10px;
  grid-template-rows: repeat(7, 90px);
  grid-template-columns: repeat(6, 200px);
  gap: 10px;
  padding: 20px;
  display: grid;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 8px 24px #212121e6;
}

.user-panel > * {
  border-radius: 10px;
  padding: 10px 20px;
}

.user-panel__section-title {
  margin-bottom: 14px;
}

@media only screen and (max-width: 1600px) {
  .user-panel {
    grid-template-rows: repeat(7, 80px);
    grid-template-columns: repeat(6, 180px);
    padding: 14px;
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media only screen and (max-width: 1024px) {
  .user-panel {
    grid-template-rows: repeat(7, 70px);
    grid-template-columns: repeat(6, 150px);
    padding: 16px;
    font-size: 14px;
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.header {
  background-color: #7e9680;
  grid-column: 1 / 7;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  display: flex;
}

.header__balance {
  font-size: 32px;
}

.header__date {
  font-size: 20px;
}

.movements {
  grid-area: 2 / 1 / 8 / 5;
  padding: 0 10px 0 0;
  overflow-x: hidden;
}

.movements::-webkit-scrollbar {
  width: 13px;
}

.movements::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 6px #2121214d;
}

.movements::-webkit-scrollbar-thumb {
  border: 1px solid #ffffff4d;
  border-radius: 10px;
}

.movements__table {
  width: 100%;
}

.movements__table td:nth-child(2) {
  width: 0;
}

.table__td--descr {
  font-size: 14px;
}

.table__tbody {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}

.table__td {
  padding: 10px;
}

.table__tr:nth-child(2n+1) {
  background-color: #bdbdbd66;
}

.table__tr:nth-child(2n) {
  background-color: #9e9e9ecc;
}

.table__td--type-deposit {
  color: #236023;
}

.table__td--type-withdrawal {
  color: #890000;
}

.transfer {
  background-color: #d87f81;
  grid-area: 2 / 5 / 4 / 7;
}

.request {
  background-color: #ae6378;
  grid-area: 4 / 5 / 6 / 7;
}

.close {
  background-color: #79616f;
  grid-area: 6 / 5 / 8 / 7;
}

.form {
  position: relative;
}

.form__label {
  margin-left: 10px;
}

.form__confirm {
  position: relative;
  left: 180px;
}

.form__btn--login {
  margin-left: 20px;
}

.form__amount, .form__close-pin {
  margin-top: 7px;
}

.footer {
  background-color: #c49c90;
  grid-column: 1 / 7;
  justify-content: space-around;
  display: flex;
}

.footer p {
  display: inline-block;
}

.footer p:nth-child(2n+1) {
  font-weight: 600;
}

.footer__in {
  color: #236023;
  font-size: 20px;
  font-weight: 900;
}

.footer__out {
  color: #890000;
  font-size: 20px;
  font-weight: 900;
}

.visibility {
  opacity: 0;
}

.hidden {
  display: none;
}

.copyright {
  color: #212121;
  font-size: .7em;
  position: absolute;
  bottom: 2%;
  right: 2%;
}

.bg {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.bg__dot {
  background: linear-gradient(to left, #9017b166, #ff009566);
  border-radius: 50%;
}

.bg__dot:nth-child(1) {
  height: 50vh;
  width: 50vh;
  animation: 15s linear infinite dotmove1;
  position: absolute;
  top: 0;
  left: 1%;
}

.bg__dot:nth-child(2) {
  height: 30vh;
  width: 30vh;
  animation: 25s linear infinite dotmove2;
  position: absolute;
  top: 50%;
  right: 2%;
}

.bg__dot:nth-child(3) {
  height: 50vh;
  width: 50vh;
  animation: 30s linear infinite dotmove3;
  position: absolute;
  top: 0;
  right: 15%;
}

.bg__dot:nth-child(4) {
  height: 30vh;
  width: 30vh;
  animation: 20s linear infinite dotmove4;
  position: absolute;
  top: 50%;
  left: 20%;
}

@keyframes dotmove1 {
  from {
    transform: translateY(-50vh);
  }

  to {
    transform: translateY(100vh);
  }
}

@keyframes dotmove2 {
  from {
    transform: translateY(50vh);
  }

  to {
    transform: translateY(-80vh);
  }
}

@keyframes dotmove3 {
  from {
    transform: translateY(-50vh);
  }

  to {
    transform: translateY(100vh);
  }
}

@keyframes dotmove4 {
  from {
    transform: translateY(50vh);
  }

  to {
    transform: translateY(-80vh);
  }
}

